// Define any SASS-only specific variables below this line

// $someVar: value;

// Specified breakpoint
@mixin media($name) {
  @include media-breakpoint-only($name) {
    @content;
  };
}

// Larger than specified breakpoint
@mixin media-up($name) {
  @include media-breakpoint-up($name) {
    @content;
  };
}

// smaller than specified breakpoint
@mixin media-down($name) {
  @include media-breakpoint-down($name) {
    @content;
  };
}

// /******************************************
// *           Placeholder
// *******************************************/
@mixin placeholder() {
  &::-webkit-input-placeholder { /* WebKit, Blink, Edge */
    @content;
  }
  &:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    @content;
  }
  &::-moz-placeholder { /* Mozilla Firefox 19+ */
    @content;
  }
  &:-ms-input-placeholder { /* Internet Explorer 10-11 */
    @content;
  }
}


@mixin debug-map($map) {
  @at-root {
    @debug-map {
      __toString__: inspect($map);
      __length__: length($map);
      __depth__: depth($map);
      __keys__: map-keys($map);
      __properties__ {
        @each $key, $value in $map {
          #{'(' + type-of($value) + ') ' + $key}: inspect($value);
        }
      }
    }
  }
}

.SidebarFooter {
  $height: $AppFooterHeight;

  height: $height;

  @include media(md) {
    .-sidebar-compact-tablet & {
      height: auto;
    }
  }

  @include media-up(lg) {
    .-sidebar-compact-desktop & {
      height: auto;
    }
  }

  .SidebarFooterNav {
    width: 100%;
    display: flex;
    flex-direction: row;

    .-rtl & {
      flex-direction: row-reverse;
    }


    @include media(md) {
      .-sidebar-compact-tablet & {
        flex-direction: column-reverse;
      }
    }

    @include media-up(lg) {
      .-sidebar-compact-desktop & {
        flex-direction: column-reverse;
      }
    }
  }

  .NavLink {
    height: $height;
    font-weight: normal;
    display: flex;
    align-items: center;
    text-decoration: none;
    justify-content: center;
    transition: background-color .3s ease, color .3s ease;
    color: var(--SidebarNavColorText);
    // background-color: rgba(255, 255, 255, 0.09);
    padding: 0;
    flex: 1;
    font-size: 30px;

    &:hover {
      color: var(--SidebarNavColorText-active);
      // background-color: rgba(255, 255, 255, 0.05);
    }
  }

  .LinkCustomize {
    .-customize & {
      background-color: #fff;
      color: var(--colorPrimary);
    }
  }

  .LinkToggleHelp {
    @include media-down(sm) {
      display: none;
    }
  }

  .LinkToggleCompact {
    font-size: 42px;

    @include media-down(sm) {
      display: none;
    }

    @include media(md) {
      .-sidebar-compact-tablet:not(.-rtl) & {
        .Icon {
          transform: rotate(180deg);
        }
      }
    }

    @include media-up(lg) {
      .-sidebar-compact-desktop:not(.-rtl) & {
        .Icon {
          transform: rotate(180deg);
        }
      }
    }

  }

}



